The CSS Reset by Josh Comeau 
https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*:not(dialog) {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* ----------------------------- */

/* GLOBAL VARIABLES DEFINITION */
:root {
  --h1-responsive: 10vw;
  --h2-responsive: 7vw;
  --h3-responsive: 5.8vw;
  --main-margin: 5vw;
  --background-color: rgb(240, 240, 240);
}

/* header */
/* ----------------------------- */
header {
  background: linear-gradient(135deg, var(--background-color) 50%, white 50%);
}

.picture-container {
  position: relative;
  text-align: left;
  margin-left: 7vw;
  margin-right: 7vw;
  margin-bottom: 7vw;
  padding-top: 7vw;
}

.bottom-left {
  position: absolute;
  bottom: 3vw;
  padding-left: 7vw;
  color: white;
  text-shadow:
    1px 1px 0 black,
    -1px 1px 0 black,
    1px -1px 0 black,
    -1px -1px 0 black;
  font-size: 13vw;
  line-height: normal;
}

.about-me h1 {
  text-align: center;
  font-size: var(--h1-responsive);
}

.about-me p {
  margin: var(--main-margin);
  font-size: 5vw;
}

.social-media-buttons {
  display: flex;
  gap: 1vw;
  justify-content: space-evenly;
}

#github-button img {
  min-width: 10vw;
}

#youtube-button img {
  min-width: 10vw;
}

/* main content of the homepage */
/* ----------------------------- */

main {
  margin-top: 17vw;
}

main h1 {
  text-align: center;
  font-size: var(--h1-responsive);
}

.main-content {
  display: flex;
  flex-direction: column;
  gap: 5vw;
}

section {
  margin: var(--main-margin);
  padding-bottom: 10vw;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

section img {
  display: flex;
}

section h2 {
  text-align: center;
  font-size: var(--h2-responsive);
  margin-left: 3vw;
  margin-right: 3vw;
  margin-top: 3vw;
}

section p {
  font-size: 4.5vw;
  margin-left: 3vw;
  margin-right: 3vw;
}

.project-title {
  display: flex;
  justify-content: space-between;
}

.project-icons {
  display: flex;
  gap: 1vw;
  padding: 3vw;
}

.container-for-img {
  position: relative;
  padding-bottom: 100%;
  cursor: zoom-in;
}

.section-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fullscreen-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: black;
  z-index: 9999;
  display: none;
  cursor: zoom-out;
}

.fullscreen-image.active {
  display: block;
}

/* footer content of the homepage */
/* ----------------------------- */

footer {
  background-color: var(--background-color);
  margin-top: 15vw;
  padding-top: 8vw;
}

.contact-me h1 {
  text-align: center;
  font-size: var(--h1-responsive);
}

.contact-me h3 {
  font-size: var(--h3-responsive);
  margin: var(--main-margin);
}

.contact-me p {
  margin: var(--main-margin);
  font-size: 5vw;
}

.phone-container {
  display: flex;
  align-items: center;
  margin-left: 5vw;
}

.phone-container p {
  display: flex;
  align-items: center;
}

.email-container {
  display: flex;
  align-items: center;
  margin-left: 5vw;
  margin-bottom: 5vw;
}

.contact-me .social-media-buttons {
  padding-bottom: 8vw;
}

/* responsive adjustments desktop */
/* ----------------------------- */

@media (min-width: 1024px) {
  header {
    display: flex;
    background: linear-gradient(155deg, var(--background-color) 50%, white 50%);
  }

  .picture-container {
    margin-right: 0vw;
  }

  .bottom-left {
    bottom: 1vw;
    padding-left: 2vw;
    font-size: 5vw;
  }

  .about-me {
    margin-top: 13vw;
    margin-bottom: 13vw;
    margin-right: 7vw;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }

  .about-me h1 {
    text-align: left;
    margin: var(--main-margin);
    font-size: 3vw;
  }

  .about-me p {
    margin: var(--main-margin);
    font-size: 2vw;
  }

  .social-media-buttons {
    display: flex;
    gap: 1vw;
    margin-right: 3vw;
    justify-content: end;
  }

  #github-button img {
    min-width: 5vw;
  }

  #youtube-button img {
    min-width: 5vw;
  }

  /*-------Main content-------*/
  main {
    margin-top: 3vw;
  }

  main h1 {
    text-align: left;
    font-size: 4vw;
    margin-left: 5vw;
  }

  .main-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2vw;
    padding: 0;
    margin-left: 5vw;
    margin-right: 5vw;
  }

  section {
    margin: 0;
  }

  section h2 {
    font-size: 3vw;
  }

  section p {
    font-size: 2vw;
    margin-top: 2vw;
  }

  .project-icons {
    padding-left: 0;
    padding-bottom: 0;
  }

  /*-------Footer-------*/
  footer {
    display: flex;
    padding-right: 5vw;
    margin-top: 5vw;
  }

  .contact-me h1 {
    font-size: 4vw;
    text-align: left;
    margin-left: 5vw;
  }

  .contact-me h3 {
    font-size: 3vw;
    text-align: left;
    margin-left: 5vw;
  }

  .contact-me p {
    font-size: 2vw;
    text-align: left;
    margin-left: 5vw;
  }

  .phone-container, .email-container {
    gap: 1vw;
  }

  .phone-container p {
    margin: 0;
  }

  .email-container p {
    margin: 0;
  }
  /* #footerPicture {
    
  } */

}
